import Aimup from "@/static/img/icon-aimup.png";
import Aimdown from "@/static/img/icon-aimdown.png";
import { getArea } from "@/api/areaApi.js";
import T from '@/utils/tips.js'
export default {
  name: "panel",
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
  		platform: 0,
      list: [],
      isMoreLocat: false,
      isMoreBtn: false,
      icon: {
        Aimup,
        Aimdown
      },
      filter: {
        place: "",
        priceBegin: "",
        priceEnd: ""
      },
      isMinOk: true,
      isMaxOk: true
    };
  },
  mounted() {
	  // 设备样式兼容
	  this.platform = uni.getStorageSync('platform');
	  console.log('platform:',this.platform)
    getArea().then(data => {
      let d = data.data;
      let res = [];
      d.forEach(item => {
        item !== null && res.push(item);
      });
      this.list = res;
    });
  },
  methods: {
	  getAreas(area){
		  this.filter.place=area.name
		  this.filter.areaId=area.id
	  },
    close() {
      this.$emit("close", false);
    },
    valiPriceBegin() {
      let val = this.filter.priceBegin;
      if (this.filter.priceBegin !== "") {
        if (!val.match(/^(^[1-9]\d+|^[0-9])(\.(\d{1,2}$))?$/)) {
          this.isMinOk = false;
          return T.tips("请输入0-9999.99的数字");
        }
        this.isMinOk = true;
      }
    },
    valiPriceEnd() {
      let val = this.filter.priceEnd;

      if (this.filter.priceEnd !== "") {
        if (!val.match(/^(^[1-9]\d+|^[0-9])(\.(\d{1,2}$))?$/)) {
          this.isMaxOk = false;
          return T.tips("请输入0-9999.99的数字");
        }
        this.isMaxOk = true;
      }
    },
    reset() {
      this.filter = {
        place: "",
        priceBegin: "",
        priceEnd: ""
      };
    },
    submit() {
      if (this.isMinOk && this.isMaxOk) {
        this.$emit('filter',this.filter)
      } else {
        this.$tips("请确认输入数据无误");
      }
    }
  }
};

